* {
  box-sizing: border-box;
}

html, body {
  height: 100%;
  margin: 0;
  padding: 0;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background: url("earth.77138fd0.jpg") center / cover no-repeat;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  width: 700px;
  color: #fff;
  background-color: rgba(32, 95, 139, .18);
  border: 2px solid #b7e3ef;
  border-radius: 25px;
  margin: 0 auto;
  padding: 8px;
  font-size: 17px;
  transition: box-shadow .3s ease-in-out;
  display: block;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .25);
}

#search-box:hover {
  box-shadow: 0 10px 20px rgba(0, 0, 0, .5);
}

h2, p {
  margin: 0;
  padding: 0;
}

ul {
  flex-flow: column-reverse wrap;
  gap: 10px;
  margin: 20px 0 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.hidden {
  display: none;
}

.country-list__item {
  width: 500px;
  color: #fff;
  background: linear-gradient(90deg, rgba(46, 206, 249, .45) .5%, rgba(46, 206, 249, .45) 25%, rgba(119, 170, 187, .4) 100%);
  border: 1px solid #fff;
  border-radius: 5px;
  align-items: center;
  gap: 7px;
  margin: 0 auto;
  padding: 10px;
  font-size: 18px;
  transition: all .3s;
  display: flex;
  box-shadow: 3px 3px 3px rgba(0, 0, 0, .3);
}

.country-list__item:hover {
  transform: scale(1.1);
  box-shadow: 5px 5px 5px rgba(0, 0, 0, .6);
}

.country-name {
  color: #fff;
  align-items: center;
  font-size: 1.2rem;
  font-weight: bold;
  display: flex;
}

.flag {
  height: auto;
  box-shadow: none;
  border: 1px solid #fff;
  border-radius: 10px;
  margin-right: 10px;
}

.capital, .population, .languages-box, .capital, .population, .languages-box p {
  color: #fff;
  font-size: 19px;
}

span {
  color: #eaffb8;
  font-size: 18px;
  font-weight: bold;
}

.languages-box p {
  color: #fff;
  display: inline-block;
}

.hover {
  color: #2c2c2c;
  background-color: #fff;
  border: 1px solid #2c2c2c;
  transition: all .3s;
}

.country-info {
  color: #fff;
  width: 498px;
  background: linear-gradient(90deg, rgba(46, 206, 249, .45) .5%, rgba(46, 206, 249, .45) 25%, rgba(119, 170, 187, .4) 100%);
  border: 1.16px solid #fff;
  border-radius: 5px;
  margin: 10px auto 0;
  padding: 10px;
  transition: all .3s;
  box-shadow: 3px 3px 3px rgba(0, 0, 0, .3);
}

.country-info:hover {
  transform: scale(1.1);
  box-shadow: 5px 5px 5px rgba(0, 0, 0, .6);
}

